import slide from 'components/slide/index'
import http from 'js/http'

export default {
  name: "index",
  components: {
    slide
  },
  data() {
    return {
      tabList: [
        {
          type: '1',
          name: '获取记录',
        },
        {
          type: '0',
          name: '使用记录'
        }
      ],
      currentType: localStorage.getItem("voteVoucherType") || '1',
      dataList: [],
      page: 1,
      loadShow: true,
      currencyInfo: {},
      voucherInfo: 0,
      total:'',
    }
  },
  methods: {
    selectedTab(item) {
      if (item.type === this.currentType) return
      this.currentType = item.type
      localStorage.setItem("voteVoucherType", item.type);
      this.page = 1
      this.dataList = []
      this.total = ''
      this.$refs.my_scroller.finishInfinite(false);
    },
    handleBottom() {
      if (this.total!==''&&this.dataList.length >= parseInt(this.total)){
        this.$refs.my_scroller.finishInfinite(true);
        return
      }

      http.post('/vote/voucher', {
        type: this.currentType,
        page: this.page,
        page_size: 10
      }, (res) => {
        if (this.loadShow) this.loadShow = false
        if (res.code !== 0) {
          this.$vux.toast.show(res.msg)
          return
        }
        this.dataList = this.dataList.concat(res.content.list)
        this.total = res.content.count
        this.page++
        this.$refs.my_scroller.finishInfinite(false);
      })
    },
    getVoucherInfo() {
      http.post('/vote/voucher-info', {}, (res) => {
        if (res.code !== 0) {
          this.$vux.toast.show(res.msg)
          return
        }
        this.voucherInfo = res.content.count
      })
    },
    getList() {
      http.post('/vote/voucher', {
        type: this.currentType,
        page: this.page,
        page_size: 10
      }, (res) => {
        if (this.loadShow) this.loadShow = false
        if (res.code !== 0) {
          this.$vux.toast.show(res.msg)
          return
        }
        /*for(let i = 0;i<20;i++){
          this.dataList = this.dataList.concat(res.content.list)
        }*/
        this.dataList = this.dataList.concat(res.content.list)
        if (this.dataList.length < parseInt(res.content.count)) {
          this.$refs.vueLoad.onBottomLoaded();
        } else {
          this.$refs.vueLoad.onBottomLoaded();
        }
      })
    },
  },
  created() {
    // this.currentType = localStorage.getItem("voteVoucherType") || '1'
    this.getVoucherInfo()
  },
}



